<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-color" id="list">
        <el-tabs>
          <el-tab-pane label='点餐'>
            <el-table :data='tableData' border style="width:100%">
              <el-table-column prop='goodsName' label='商品名称'></el-table-column>
              <el-table-column prop='count' label='数量' width='70'></el-table-column>
              <el-table-column prop='price' label='金额' width='70'></el-table-column>
              <el-table-column label='操作' width='100 ' fixed='right'>
                <template scope="scope">
                  <el-button type='text' size='small' @click="delateGoods(scope.row)">删除</el-button>
                  <el-button type='text' size='small' @click="addOrderList(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="total">
              <span>数量：{{totalCount}}</span>
              <span>金额：{{totalMoney}}</span>
            </div>
            <div class="div-btn">
              <el-button type='warning'>挂单</el-button>
              <el-button type='danger' @click='delAll()'>清空</el-button>
              <el-button type='success' @click="checkout()">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label='挂单'>
            挂单
          </el-tab-pane>
          <el-tab-pane label='外卖'>
            外卖
          </el-tab-pane>
        </el-tabs>

      </el-col>

      <el-col span='17'>
        <div class="onten-goods">
          <div class="title">常用商品</div>
          <div class="often-list">
            <ul>
              <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="often-price">￥{{goods.price}}元</span>
              </li>
            </ul>
          </div>

        </div>
        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label='汉堡'>

              <ul class="hanList">
                <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                  <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                  <span class="foodName">{{goods.goodsName}} </span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>

            </el-tab-pane>
            <el-tab-pane label='小食'>
              <ul class="hanList">
                <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                  <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                  <span class="foodName">{{goods.goodsName}} </span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label='饮料'>
              <ul class="hanList">
                <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                  <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                  <span class="foodName">{{goods.goodsName}} </span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label='套餐'>
              <ul class="hanList">
                <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                  <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                  <span class="foodName">{{goods.goodsName}} </span>
                  <span class="foodPrice">￥{{goods.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'pos',
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalCount: 0,
      totalMoney: 0
    }
  },
  created: function() {
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
      .then(response => {
        this.oftenGoods = response.data;
      })

      .catch(error => {
        alert('Internet Error')
      })


    axios.get('http://jspang.com/DemoApi/typeGoods.php')
      .then(response => {
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })

      .catch(error => {
        alert('Internet Error')
      })
  },
  mounted: function() {
    var listHerght = document.body.clientHeight;
    document.getElementById('list').style.height = listHerght + 'px';
  },
  methods: {
    //添加订单列表的方法
    addOrderList(goods) {
      //console.log(goods)
      this.totalCount = 0;
      this.totalMoney = 0;
      let isHave = false;
      //判断是否这个商品已经存在于订单列表
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      //根据isHave的值判断订单列表中是否已经有此商品
      if (isHave) {
        let arr = this.tableData.filter(a => a.goodsId == goods.goodsId)
        arr[0].count++
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods)

      }
      this.getAllMoney()
    },
    //删除单个商品
    delateGoods(goods) {

      this.tableData = this.tableData.filter(a => a.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    //删除全部商品
    delAll() {
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    getAllMoney() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        this.tableData.forEach((element) => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + (element.price * element.count)
        })
      }
    },
    checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message.success('结账成功')
      }else{
        this.$message.error('请添加商品')
      }
    }


  }

}
</script>


<style scoped>
.pos-color {
  background: #f9fafc;
  border-right: 1px solid #c0ccda;
}

.div-btn {
  margin-top: 10px;
}

.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
  text-align: left;
}

.often-list ul li {
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
}

.often-price {
  color: #58b7ff;
}

.goods-type {
  clear: both;
}

.hanList li {
  list-style: none;
  width: 23%;
  border: 1px solid #E5E9F2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
}

.hanList li span {
  display: block;
  float: left;
}

.foodImg {
  width: 40%;
}

.foodName {
  font-size: 10px;
  padding-left: 10px;
  color: brown;
}

.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}

.total {
  border-bottom: 1px solid #E5E9F2;
}

.total span {
  margin: 10px 0;
  display: inline-block;
  width: 70px;
  font-size: 13px;
}
</style>



